.footer {
  font-size: xx-large;
  height: 2.5vw;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background: rgb(28,23,105);
  background: linear-gradient(90deg, rgba(28,23,105,1) 0%, rgba(28,28,171,1) 35%, rgba(212,86,207,1) 59%, rgba(146,237,255,1) 100%);
  color: white;
  text-align: center;
  -webkit-box-shadow: 0 0 18px 3px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0 0 18px 3px rgba(0, 0, 0, 0.75);
  box-shadow: 0 0 18px 3px rgba(0, 0, 0, 0.75);
}